
.about {
  padding-bottom: 0px;
}

.about__container {
  background-color: #84a178;
  color: #fcfcfc;
  padding-top: 60px;
  padding-bottom: 21px;
}
.about__suptitle {
  @media screen and (max-width: 767px) {
    font-weight: 700;
  }
}
.about__title {
  margin-bottom: 32px;
  @media screen and (min-width: 768px) and (max-width: 1199px) {
    letter-spacing: 0.06em;
    margin-bottom: 72px;
  }
  @media screen and (min-width: 1200px) {
    margin-bottom: 82px;
  }
}
.about__content {
  @media screen and (min-width: 768px) {
    display: flex;
    gap: 32px;
  }
  @media screen and (min-width: 1200px) {
    gap: 100px;
  }
}
.about__paragraphs {
  @media screen and (max-width: 767px) {
    text-align: center;
    margin-top: 40px;
  }
  @media screen and (min-width: 768px) {
    width: 336px;
  }
  @media screen and (min-width: 1200px) {
    width: 463px;
  }
}
.about__text {
  @media screen and (max-width: 1199px) {
    font-size: 12px;
  }
}
.about__text-top {
  @media screen and (max-width: 767px) {
    margin-top: 40px;
  }
}
.about__text-bottom {
  @media screen and (max-width: 767px) {
    line-height: 1.58;
  }
}
.box {
  position: absolute;
  display: flex;
  background: linear-gradient(
      0deg,
      rgba(248, 210, 78, 0.7),
      rgba(248, 210, 78, 0.7)
    ),
    #d9d9d9;
  box-shadow: 0px 8px 30px rgba(153, 114, 23, 0.25);
  height: 278px;
  width: 288px;
  border-radius: 140.182px;

  @media screen and (min-width: 768px) {
    height: 334px;
    width: 334px;
    border-radius: 165.879px;
  }

  @media screen and (min-width: 1200px) {
    height: 500px;
    width: 489px;
    border-radius: 250px;
  }
}

.about__img {
  position: relative;
  top: 21px;

  @media screen and (min-width: 768px) {
    top: 25px;
  }
  @media screen and (min-width: 1200px) {
    top: 39px;
  }
}
